html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: sans-serif;
  font-weight: 400;
}

::-moz-selection {
  color: #fff;
  background: rgba(68, 68, 68, 0.8);
}

::selection {
  color: #fff;
  background: rgba(68, 68, 68, 0.8);
}

.App {
  text-align: center;
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: #000;
  background: -webkit-linear-gradient(rgba(43, 14, 61, 0.7), rgba(19, 11, 42, 0.7)), url("../public/background.webp");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.App-header {
  position: relative;
  z-index: 2;
  color: #fff;
}

h1 {
  font-family: EB Garamond, serif;
  font-size: 4rem;
  font-weight: 600;
  letter-spacing: -.015em;
  line-height: 1;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.particle {
  position: absolute;
  width: 0.35rem;
  height: 0.35rem;
  border-radius: 50%;
  background-color: white;
  transform-origin: center;
  -webkit-filter: blur(0.1rem);
  filter: blur(0.1rem);
}
